"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { ExternalLink, Search, Settings, Plus } from "lucide-react"
import AdminSection from "@/components/admin-section"

export interface Course {
  id: string
  name: string
  type: "free" | "paid"
  company: string
  description: string
  imageUrl: string
  courseUrl: string
}

export default function CoursesPage() {
  const [courses, setCourses] = useState<Course[]>([])
  const [filteredCourses, setFilteredCourses] = useState<Course[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [typeFilter, setTypeFilter] = useState<"all" | "free" | "paid">("all")
  const [showAdmin, setShowAdmin] = useState(false)

  // Load courses from localStorage on component mount
  useEffect(() => {
    const savedCourses = localStorage.getItem("certiswift-courses")
    if (savedCourses) {
      const parsed = JSON.parse(savedCourses)
      setCourses(parsed)
      setFilteredCourses(parsed)
    } else {
      // Default sample courses
      const defaultCourses: Course[] = [
        {
          id: "1",
          name: "React Fundamentals",
          type: "free",
          company: "Meta",
          description: "Learn the basics of React including components, state, and props. Perfect for beginners starting their React journey.",
          imageUrl: "/api/placeholder/300/200",
          courseUrl: "https://react.dev/learn"
        },
        {
          id: "2",
          name: "Advanced JavaScript",
          type: "paid",
          company: "Google",
          description: "Deep dive into advanced JavaScript concepts including closures, async/await, and modern ES6+ features.",
          imageUrl: "/api/placeholder/300/200",
          courseUrl: "https://javascript.info"
        },
        {
          id: "3",
          name: "Python for Data Science",
          type: "free",
          company: "Microsoft",
          description: "Introduction to data science using Python, covering pandas, numpy, and basic machine learning concepts.",
          imageUrl: "/api/placeholder/300/200",
          courseUrl: "https://python.org"
        }
      ]
      setCourses(defaultCourses)
      setFilteredCourses(defaultCourses)
      localStorage.setItem("certiswift-courses", JSON.stringify(defaultCourses))
    }
  }, [])

  // Filter courses based on search term and type
  useEffect(() => {
    let filtered = courses

    if (searchTerm) {
      filtered = filtered.filter(course =>
        course.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        course.company.toLowerCase().includes(searchTerm.toLowerCase()) ||
        course.description.toLowerCase().includes(searchTerm.toLowerCase())
      )
    }

    if (typeFilter !== "all") {
      filtered = filtered.filter(course => course.type === typeFilter)
    }

    setFilteredCourses(filtered)
  }, [courses, searchTerm, typeFilter])

  const handleCourseUpdate = (updatedCourses: Course[]) => {
    setCourses(updatedCourses)
    localStorage.setItem("certiswift-courses", JSON.stringify(updatedCourses))
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 to-orange-100">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">
            Certiswift Courses
          </h1>
          <p className="text-lg text-gray-600 max-w-2xl mx-auto">
            Discover and learn from industry-leading courses. Enhance your skills with free and premium content from top companies.
          </p>
        </div>

        {/* Controls */}
        <div className="flex flex-col sm:flex-row gap-4 mb-8 items-center justify-between">
          <div className="flex flex-col sm:flex-row gap-4 flex-1 max-w-2xl">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <Input
                placeholder="Search courses, companies, or topics..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10"
              />
            </div>
            <Select value={typeFilter} onValueChange={(value: "all" | "free" | "paid") => setTypeFilter(value)}>
              <SelectTrigger className="w-[140px]">
                <SelectValue placeholder="Filter type" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All Courses</SelectItem>
                <SelectItem value="free">Free Only</SelectItem>
                <SelectItem value="paid">Paid Only</SelectItem>
              </SelectContent>
            </Select>
          </div>
          
          <Button 
            onClick={() => setShowAdmin(true)} 
            variant="outline"
            className="flex items-center gap-2"
          >
            <Settings className="w-4 h-4" />
            Admin
          </Button>
        </div>

        {/* Course Grid */}
        {filteredCourses.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-gray-500 mb-4">
              <Search className="w-16 h-16 mx-auto mb-4 opacity-50" />
              <h3 className="text-xl font-semibold mb-2">No courses found</h3>
              <p>Try adjusting your search terms or filters</p>
            </div>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredCourses.map((course) => (
              <Card key={course.id} className="overflow-hidden hover:shadow-lg transition-shadow">
                <div className="aspect-video bg-gradient-to-r from-orange-400 to-orange-600 relative">
                  <img
                    src={course.imageUrl}
                    alt={course.name}
                    className="w-full h-full object-cover"
                    onError={(e) => {
                      e.currentTarget.src = `data:image/svg+xml,${encodeURIComponent(`
                        <svg xmlns="http://www.w3.org/2000/svg" width="300" height="200" viewBox="0 0 300 200">
                          <rect width="300" height="200" fill="url(#gradient)"/>
                          <defs>
                            <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="100%">
                              <stop offset="0%" style="stop-color:#fb923c;stop-opacity:1" />
                              <stop offset="100%" style="stop-color:#ea580c;stop-opacity:1" />
                            </linearGradient>
                          </defs>
                          <text x="150" y="100" text-anchor="middle" dominant-baseline="middle" fill="white" font-family="Arial, sans-serif" font-size="18" font-weight="bold">${course.name}</text>
                        </svg>
                      `)}`
                    }}
                  />
                  <div className="absolute top-3 right-3">
                    <Badge variant={course.type === "free" ? "secondary" : "default"}>
                      {course.type.toUpperCase()}
                    </Badge>
                  </div>
                </div>
                <CardHeader>
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <CardTitle className="line-clamp-2">{course.name}</CardTitle>
                      <CardDescription className="text-orange-600 font-medium">
                        {course.company}
                      </CardDescription>
                    </div>
                  </div>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-gray-600 line-clamp-3">
                    {course.description}
                  </p>
                </CardContent>
                <CardFooter>
                  <Button asChild className="w-full">
                    <a 
                      href={course.courseUrl} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      className="flex items-center gap-2"
                    >
                      View Course
                      <ExternalLink className="w-4 h-4" />
                    </a>
                  </Button>
                </CardFooter>
              </Card>
            ))}
          </div>
        )}

        {/* Stats */}
        <div className="mt-12 grid grid-cols-1 sm:grid-cols-3 gap-4">
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <div className="text-2xl font-bold text-orange-600">
                  {filteredCourses.length}
                </div>
                <p className="text-sm text-gray-600">Available Courses</p>
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <div className="text-2xl font-bold text-green-600">
                  {filteredCourses.filter(c => c.type === "free").length}
                </div>
                <p className="text-sm text-gray-600">Free Courses</p>
              </div>
            </CardContent>
          </Card>
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">
                  {filteredCourses.filter(c => c.type === "paid").length}
                </div>
                <p className="text-sm text-gray-600">Premium Courses</p>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Admin Section */}
      <AdminSection
        isOpen={showAdmin}
        onClose={() => setShowAdmin(false)}
        courses={courses}
        onCoursesUpdate={handleCourseUpdate}
      />
    </div>
  )
}
