"use client"

import { useState } from "react"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import { Plus, Trash2, Eye, EyeOff } from "lucide-react"
import { Course } from "@/app/courses/page"

interface AdminSectionProps {
  isOpen: boolean
  onClose: () => void
  courses: Course[]
  onCoursesUpdate: (courses: Course[]) => void
}

export default function AdminSection({ isOpen, onClose, courses, onCoursesUpdate }: AdminSectionProps) {
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [showAddForm, setShowAddForm] = useState(false)
  const [deleteId, setDeleteId] = useState<string | null>(null)
  
  // Form state
  const [formData, setFormData] = useState({
    name: "",
    type: "free" as "free" | "paid",
    company: "",
    description: "",
    imageUrl: "",
    courseUrl: ""
  })

  const handleAuth = () => {
    if (password === "pikachu") {
      setIsAuthenticated(true)
      setPassword("")
    } else {
      alert("Incorrect password!")
      setPassword("")
    }
  }

  const handleClose = () => {
    setIsAuthenticated(false)
    setPassword("")
    setShowAddForm(false)
    setFormData({
      name: "",
      type: "free",
      company: "",
      description: "",
      imageUrl: "",
      courseUrl: ""
    })
    onClose()
  }

  const handleAddCourse = () => {
    if (!formData.name || !formData.company || !formData.description || !formData.courseUrl) {
      alert("Please fill in all required fields")
      return
    }

    const newCourse: Course = {
      id: Date.now().toString(),
      ...formData
    }

    const updatedCourses = [...courses, newCourse]
    onCoursesUpdate(updatedCourses)
    
    setFormData({
      name: "",
      type: "free",
      company: "",
      description: "",
      imageUrl: "",
      courseUrl: ""
    })
    setShowAddForm(false)
  }

  const handleDeleteCourse = (id: string) => {
    const updatedCourses = courses.filter(course => course.id !== id)
    onCoursesUpdate(updatedCourses)
    setDeleteId(null)
  }

  if (!isOpen) return null

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Admin Panel</DialogTitle>
          <DialogDescription>
            Manage courses for Certiswift
          </DialogDescription>
        </DialogHeader>

        {!isAuthenticated ? (
          <div className="space-y-4">
            <div className="text-center">
              <h3 className="text-lg font-semibold mb-4">Enter Admin Password</h3>
              <div className="max-w-sm mx-auto space-y-4">
                <div className="relative">
                  <Input
                    type={showPassword ? "text" : "password"}
                    placeholder="Password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    onKeyPress={(e) => e.key === "Enter" && handleAuth()}
                  />
                  <Button
                    type="button"
                    variant="ghost"
                    size="sm"
                    className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                    onClick={() => setShowPassword(!showPassword)}
                  >
                    {showPassword ? (
                      <EyeOff className="h-4 w-4" />
                    ) : (
                      <Eye className="h-4 w-4" />
                    )}
                  </Button>
                </div>
                <Button onClick={handleAuth} className="w-full">
                  Login
                </Button>
              </div>
            </div>
          </div>
        ) : (
          <div className="space-y-6">
            {/* Add Course Section */}
            <div className="border-b pb-4">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold">Course Management</h3>
                <Button 
                  onClick={() => setShowAddForm(!showAddForm)}
                  className="flex items-center gap-2"
                >
                  <Plus className="w-4 h-4" />
                  Add Course
                </Button>
              </div>

              {showAddForm && (
                <Card>
                  <CardHeader>
                    <CardTitle>Add New Course</CardTitle>
                    <CardDescription>Fill in the course details below</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <label className="text-sm font-medium">Course Name *</label>
                        <Input
                          placeholder="e.g., Advanced React Patterns"
                          value={formData.name}
                          onChange={(e) => setFormData({...formData, name: e.target.value})}
                        />
                      </div>
                      <div>
                        <label className="text-sm font-medium">Company *</label>
                        <Input
                          placeholder="e.g., Meta, Google, Microsoft"
                          value={formData.company}
                          onChange={(e) => setFormData({...formData, company: e.target.value})}
                        />
                      </div>
                    </div>
                    
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <label className="text-sm font-medium">Type *</label>
                        <Select value={formData.type} onValueChange={(value: "free" | "paid") => setFormData({...formData, type: value})}>
                          <SelectTrigger>
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="free">Free</SelectItem>
                            <SelectItem value="paid">Paid</SelectItem>
                          </SelectContent>
                        </Select>
                      </div>
                      <div>
                        <label className="text-sm font-medium">Course Image URL</label>
                        <Input
                          placeholder="https://example.com/image.jpg"
                          value={formData.imageUrl}
                          onChange={(e) => setFormData({...formData, imageUrl: e.target.value})}
                        />
                      </div>
                    </div>

                    <div>
                      <label className="text-sm font-medium">Description *</label>
                      <Textarea
                        placeholder="Describe what students will learn..."
                        value={formData.description}
                        onChange={(e) => setFormData({...formData, description: e.target.value})}
                        rows={3}
                      />
                    </div>

                    <div>
                      <label className="text-sm font-medium">Course URL *</label>
                      <Input
                        placeholder="https://example.com/course"
                        value={formData.courseUrl}
                        onChange={(e) => setFormData({...formData, courseUrl: e.target.value})}
                      />
                    </div>

                    <div className="flex gap-2 pt-2">
                      <Button onClick={handleAddCourse}>Add Course</Button>
                      <Button variant="outline" onClick={() => setShowAddForm(false)}>Cancel</Button>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>

            {/* Existing Courses */}
            <div>
              <h3 className="text-lg font-semibold mb-4">Existing Courses ({courses.length})</h3>
              {courses.length === 0 ? (
                <p className="text-gray-500 text-center py-8">No courses available. Add your first course above!</p>
              ) : (
                <div className="space-y-3 max-h-96 overflow-y-auto">
                  {courses.map((course) => (
                    <Card key={course.id}>
                      <CardContent className="pt-4">
                        <div className="flex items-start justify-between">
                          <div className="flex-1 min-w-0">
                            <div className="flex items-center gap-2 mb-2">
                              <h4 className="font-semibold truncate">{course.name}</h4>
                              <Badge variant={course.type === "free" ? "secondary" : "default"}>
                                {course.type.toUpperCase()}
                              </Badge>
                            </div>
                            <p className="text-sm text-orange-600 font-medium mb-1">{course.company}</p>
                            <p className="text-sm text-gray-600 line-clamp-2">{course.description}</p>
                            <p className="text-xs text-blue-600 mt-2 truncate">
                              <a href={course.courseUrl} target="_blank" rel="noopener noreferrer" className="hover:underline">
                                {course.courseUrl}
                              </a>
                            </p>
                          </div>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => setDeleteId(course.id)}
                            className="ml-4 flex-shrink-0"
                          >
                            <Trash2 className="w-4 h-4" />
                          </Button>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </DialogContent>

      {/* Delete Confirmation Dialog */}
      <AlertDialog open={!!deleteId} onOpenChange={() => setDeleteId(null)}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Delete Course</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to delete this course? This action cannot be undone.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={() => deleteId && handleDeleteCourse(deleteId)}
              className="bg-red-600 hover:bg-red-700"
            >
              Delete
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </Dialog>
  )
}
